import { Button } from 'antd'
import { Link, useHistory } from 'react-router-dom'
import notAuthImg from '@src/assets/images/illustration/restricted.svg'
import { ReactComponent as ArrowLeft } from '@src/assets/images/icons/arrow-left.svg'
import { FormattedMessage } from 'react-intl'

import '@styles/base/pages/page-misc.scss'
import './styles/NotAuthorized.scss'

const NotAuthorized = () => {
  const history = useHistory()
  return (
    <div className='misc-wrapper'>
        <div className='w-100 text-center'>
          <img className='img-fluid' src={notAuthImg} alt='Not authorized page' />
          <h2 className='py-3 not-authorized-title'>Access restricted</h2>
          <p className='pb-3'>
            <FormattedMessage id='restrictedMessage' />
          </p>
          <button className="btn-auth-back" onClick={() => history.goBack()}>
            <ArrowLeft className="mr-1" />
            <FormattedMessage id="goBack" />
          </button>
        </div>
      <div className='misc-inner p-2 p-sm-3'>
      </div>
    </div>
  )
}
export default NotAuthorized
